.date {
  position: relative;
  background-color: #81a5bf;
  font-family: Arial, Helvetica, sans-serif;
  color: #FFFFFF;
  margin-right: 16px;
  padding: 5px;
  text-align: center;
  text-transform: uppercase;
  float: left;
}
.date span.day {
  display: block;
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
}
.date span.year {
  display: block;
  font-size: 10px;
  line-height: 10px;
}
.date:after {
  left: 100%;
  top: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-left-color: #81a5bf;
  border-width: 8px;
  margin-top: -8px;
}

.pagination {
  --bs-pagination-padding-x: 0.75rem;
  --bs-pagination-padding-y: 0.375rem;
  --bs-pagination-font-size: 1rem;
  --bs-pagination-color: #81a5bf;
  --bs-pagination-bg: #FFFFFF;
  --bs-pagination-border-color: #81a5bf;
  --bs-pagination-hover-color: #FFFFFF;
  --bs-pagination-hover-bg: #81a5bf;
  --bs-pagination-hover-border-color: #81a5bf;
  --bs-pagination-focus-color: #FFFFFF;
  --bs-pagination-focus-bg: #81a5bf;
  --bs-pagination-active-color: #FFFFFF;
  --bs-pagination-active-bg: #81a5bf;
  --bs-pagination-active-border-color: #81a5bf;
  --bs-pagination-disabled-color: #81a5bf;
  --bs-pagination-disabled-bg: #FFFFFF;
  --bs-pagination-disabled-border-color: #81a5bf;
}

.view .views-row {
  margin-bottom: 15px;
}

.view-articles .views-row,
.view-taxonomy-term .views-row {
  padding-bottom: 7px;
  border-bottom: 1px dashed #ddd;
}
.view-articles .field--name-node-title h2,
.view-taxonomy-term .field--name-node-title h2 {
  margin-top: 0;
}
.view-articles .field--name-field-image-media img,
.view-taxonomy-term .field--name-field-image-media img {
  width: 100%;
  border: 1px solid #eee;
  margin: 0 auto;
}
.view-articles .field-name-body p:first-child:first-letter,
.view-taxonomy-term .field-name-body p:first-child:first-letter {
  font-family: "Times New Roman", Times, serif;
  font-size: 200%;
  color: #293749;
}

@media (min-width: 768px) {
  .view-articles .field--name-field-image-media,
  .view-taxonomy-term .field--name-field-image-media {
    max-width: 200px;
    float: left;
    margin: 0 15px 15px 0;
  }
}